export default {
	data() {
		return {
			xinxi:[],
			stute:[]
		}
	},
	onLoad() {
		
		uni.request({
			url: 'http://106.13.239.202/index.php?c=payment&m=get',
			method: 'POST',
			header: {"content-type": "application/x-www-form-urlencoded"}, 
			data: {
				
				phone:"13540108723"
				
				
			},
			success: res => {
				var i = 0;
				console.log(res.data.data)
				
			     this.xinxi = res.data.data;
				 	while(i<res.data.data.length)
				 	{
				 		if(this.xinxi[i].stute==1)
				 		{
				 			this.stute[i] = "已缴费";
				 		}
				 		if(this.xinxi[i].stute==0)
				 		{
				 			this.stute[i] = "未缴费";
				 		}
				 		
				 		i++;
				 	}
			},
			fail: () => {
			
			},
			complete: () => {}
		});
		
	},
	methods: {
		
		details (item)
		{
			let phone = item.phone;
		     console.log(phone)
			uni.navigateTo({
				url: `/pages/payment/details?context=${phone}`
			})
		}
		  
		}
	}

